<template>
  <div class="login">
    <h2>Log in to Trello</h2>
    <form @submit.prevent="onSubmit">
      <div>
        <label for="email">Email</label>
        <input class="form-control" type="text" name="email" 
          v-model="email" autofocus />
      </div>
      <div>
        <label for="password">Passwrod</label>
        <input class="form-control" type="password"
          v-model="password" placeholder="123123" />
      </div>
      <button  class="btn" type="submit" 
        v-on:click="onLogin"></button>
      <button class="btn" v-on:click="onJoin" type="submit"></button>
    </form>
  </div>
</template>

<script>
import {auth, setAuthInHeader} from '../api'
import {mapActions,mapState} from 'vuex'
export default {
  data() {
    return {
      email: '',
      password: '',
      rPath:'',
      clickState:'',
    }
  },
  
  created() {
    this.rPath = this.$route.query.rPath || '/'
  },
  methods: {
    ...mapActions([
      'LOGIN',
      'JOIN'
    ]),
    onLogin(){
      this.clickState = 'login'
    },
    onJoin(){
      this.clickState = 'join'
    },
    onSubmit() {
      if(this.clickState==='login'){
      
        this.LOGIN({email:this.email,password:this.password}) //JOIN .
          .then(data => {
            this.$router.push('/')
          })
          .catch(err=>{
            this.error = err.data.error
          })
      }
      else if(this.clickState='join'){
        this.JOIN({email:this.email,password:this.password}) //JOIN .
          .then(data => {
            this.$router.push(this.rPath)
          })
          .catch(err=>{
            this.error = err.data.error
          })
      }
    },
    
  }
}
</script>

<style>
.login {
  width: 400px;
  margin: 0 auto; 
}
.error {
  color: #f00;
}
</style>


